import React, {useEffect, useState} from 'react';
import styled from '@emotion/styled';
import { minWidth } from 'values/styleValue';

const Container = styled.div`
  width:  125px;
  height: 125px;
  border: 5px solid #fff;
  border-radius: 72.5px;

  display: inline-block;
  position: relative;

  @media screen and (min-width: ${minWidth}px){
    width:  250px;
    height: 250px;
    border-radius: 125px;
  }
`
const HourBar = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  outline: 5px solid #fff;
  height: 25%;
  transform-origin: top;
  transform: rotateZ(${props => props.degree}deg);
`
const MinuteBar = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  outline: 3px solid #fff;
  height: 36.66%;
  transform-origin: top;
  transform: rotateZ(${props => props.degree}deg);
`
const SecondBar = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  outline: 1px solid #fff;
  height: 43.33%;
  transform-origin: top;
  transform: rotateZ(${props => props.degree}deg);
`

const AnalogClock = () => {
  const [date, setDate] = useState(new Date());

  useEffect(() => {
    let interval = setInterval(() => {
      setDate(new Date());
    }, 1000)
    return () => {
      clearInterval(interval);
    }
  }, [])

  const getDegree = () => {
    let sec = (date.getSeconds() * 6);
    let min = (date.getMinutes() * 6) + (sec / 60);
    let hour = (date.getHours() * 30) + (min / 12);

    sec += 180;
    min += 180;
    hour += 180;

    return {hour, min, sec};
  }

  const degree = getDegree();

  return (
    <Container>
      <HourBar degree={degree.hour}/>
      <MinuteBar degree={degree.min}/>
      <SecondBar degree={degree.sec}/>
    </Container>
  )
}
export default AnalogClock;