import React from 'react';

import Stack from '@mui/material/Stack';

/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import styled from '@emotion/styled';

import TitleText from 'components/common/TitleText';

const Container = styled.div`
  display: flex;
  flex-direction: column;
`

const schoolNameStyle = css`
  font-size: 18px;
  font-weight: bold;
`
const majorNameStyle = css`
  margin-left: 14px;
  font-size: 14px;
  color: #999;
`
const durationStyle = css`
  font-size: 14px;
  margin-left: 8px;
`
const stackStyle = css `
  padding: 0px 10px;
`

const Contents = ({schoolName, majorName, duration}) => (
  <div>
    <div css={schoolNameStyle}>{schoolName}<span css={majorNameStyle}>{majorName}</span></div>
    <div css={durationStyle}>
      {duration}
    </div>
  </div>
)

const Academic = ({academicData = []}) => {
  return (
    <Container>
      <TitleText>학력</TitleText>
      <Stack css={stackStyle} spacing={1}>
        {academicData.map((data, index) => (
          <Contents
            key={index}
            schoolName={data.schoolName}
            majorName={data.majorName}
            duration={data.duration}
          />
        ))}
      </Stack>
    </Container>
  )
}

export default Academic;