import React, {useEffect, useState} from 'react';

import AnalogClock from 'components/home/AnalogClock';
import DigitalClock from 'components/home/DigitalClock';

import { maxWidth, minWidth } from 'values/styleValue';

import dayjs from 'dayjs';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import styled from '@emotion/styled';
import { get } from 'common/Ajax';

const containerStyle = css`

`
const clocksStyle = css`
  display: flex;
  align-items: center;
  @media screen and (max-width: ${maxWidth}px){
    flex-direction: column;
    align-items: center;
  }
  @media screen and (min-width: ${minWidth}px){
    justify-content: center;
  }
`
const weatherContainer = css`
  margin-top: 28px;
  font-family: 'Nanum Gothic Coding';
`
const weatherListStyle = css`
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  justify-content: start;
`
const weatherBoxStyle = css`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #666;
  padding: 4px;
  margin: 4px;
  border-radius: 8px;
`
const WeatherIcon = styled.div`
  width: 75px;
  height: 75px;
  background-image: url( ${props => props.url} );
  background-size: 75px;
  background-repeat: no-repeat;
  background-position: center;
`

const getWeatherUrl = () => {
  let weatherUrl = 'https://api.openweathermap.org/data/2.5/forecast';
  const weatherParams = {
    lat: 35.016903,
    lon: 126.790376,
    lang: 'kr',
    units: 'metric',
    appid: 'c79f07006398458cf212f43f9a609f64'
  }
  let params = [];
  for(let key in weatherParams) {
    params.push(`${key}=${weatherParams[key]}`);
  }
  weatherUrl += '?' + params.join('&');
  return weatherUrl;
}

const Home = () => {
  const [weathers, setWeathers] = useState([]);
  
  useEffect(() => {
    // get('weather.json')
    get(getWeatherUrl())
    .then(data => {
      setWeathers(
        weathersProcessing(
          data.list.map((item, index) => {
            const dayVal = dayjs(new Date(item.dt * 1000));
            return {
              date: dayVal,
              ymd: dayVal.format('YYYYMMDD'),
              temp: item.main.temp,
              feels_like: item.main.feels_like,
              icon: item.weather[0].icon,
            }
          })
        )
      )
    })
    .catch(err => {
      console.error(err)
    })
  }, [])

  const weathersProcessing = (data) => {
    let arr = [];
    for(var i=0; i<data.length; i++) {
      let ymd = data[i].ymd;
      if(arr.findIndex(item => item.ymd === ymd) === -1) {
        arr.push({
          ymd: ymd,
          dateStr: data[i].date.format('M월 D일'),
          list: []
        })
      }
      arr[arr.length - 1].list.push({
        time: data[i].date.format('H시'),
        temp: data[i].temp,
        feels_like: data[i].feels_like,
        icon: data[i].icon,
      })
    }
    return arr;
  }

  useEffect(() => {
    if(weathers.length > 0) console.log(weathers)
  }, [weathers])

  return (
    <div css={containerStyle}>
      <div css={clocksStyle}>
        <AnalogClock/>
        <DigitalClock/>
      </div>
      <div css={weatherContainer}>
        {weathers.map((weather) => (
          <div key={weather.ymd}>
            <div>{weather.dateStr}</div>
            <div css={weatherListStyle}>
              {weather.list.map((item, idx) => (
                <div key={idx} css={weatherBoxStyle}>
                  <div>{item.time}</div>
                  <WeatherIcon url={`http://openweathermap.org/img/wn/${item.icon}@2x.png`}/>
                  <div>{item.temp}℃</div>
                  {/* <div>체감온도: {item.feels_like}</div> */}
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
export default Home;