import * as React from 'react';
import SwipeableViews from 'react-swipeable-views';
// import { useTheme } from '@mui/material/styles';
import { AppBar, Tabs, Tab, Box } from '@mui/material'
import Slide from '@mui/material/Slide';
import useScrollTrigger from '@mui/material/useScrollTrigger';

/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import styled from '@emotion/styled'

import HomeIcon from '@mui/icons-material/Home';
import ArticleIcon from '@mui/icons-material/Article';
import BuildIcon from '@mui/icons-material/Build';
import PersonIcon from '@mui/icons-material/Person';

const container = css`
  min-height: 100%;
  display: flex;
  flex-direction: column;
  ${'' /* background-color: #ccf; */}
  ${'' /* background-color: #1e1e1e; */}
  background-color: transparent;
`
const contentBox = css`
  flex-grow: 1;
`
const Content = styled.div`
  padding: 18px;
  padding-bottom: 100px;
`
const appBar = css`
  ${'' /* background-color: #77f; */}
  background-color: #111;
  position: fixed;
  bottom: 0px;
  left: 0px;
`

function HideOnScroll(props) {
  const { children, window } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
  });

  return (
    <Slide appear={false} direction="up" in={!trigger}>
      {children}
    </Slide>
  );
}


export default function FullWidthTabs({element1, element2, element3, element4}) {
  // const theme = useTheme();
  const [value, setValue] = React.useState(0);

  return (
    <Box css={container} sx={{ bgcolor: 'background.paper'}}>
      <SwipeableViews
        css={contentBox}
        index={value}
        onChangeIndex={(index) => {setValue(index)}}
      >
        <Content>{value === 0 && element1}</Content>
        <Content>{value === 1 && element2}</Content>
        <Content>{value === 2 && element3}</Content>
        <Content>{value === 3 && element4}</Content>
      </SwipeableViews>
      <HideOnScroll>
        <AppBar css={appBar} position="static">
          <Tabs
            value={value}
            onChange={(event, newValue) => {setValue(newValue)}}
            indicatorColor="primary"
            textColor="inherit"
            variant="fullWidth"
            // centered={true}
            aria-label="full width tabs example"
          >
            <Tab icon={<HomeIcon/>} label="HOME"/>
            <Tab icon={<ArticleIcon/>} label="POST"/>
            <Tab icon={<BuildIcon/>} label="TOOL"/>
            <Tab icon={<PersonIcon/>} label="PROFILE"/>
          </Tabs>
        </AppBar>
      </HideOnScroll>
    </Box>
  );
}
