import React, {useState, useEffect} from 'react';
import styled from '@emotion/styled';
import { minWidth } from 'values/styleValue';

import dayjs from 'dayjs';
import 'dayjs/locale/ko'
dayjs.locale('ko');


const Container = styled.div`
  padding: 8px 0px 8px 8px;
  display: inline-block;
  height: 130px;
  font-family: 'Nanum Gothic', Courier, monospace;

  display: grid;
  align-items: center;
  grid-template-columns: 70px 1fr;
  grid-template-rows: 60px 23px 23px;

  @media screen and (min-width: ${minWidth}px) {
    padding: 38px 0px 18px 38px;
  }
`
const TimeText = styled.div`
  grid-column: 1 / span 2;
  font-size: 32px;
  font-weight: bold;
  
  @media screen and (min-width: ${minWidth}px) {
    font-size: 48px;
  }
`
const MeridiemText = styled.div`
  grid-row: 2 / span 2;
  font-size: 34px;
`
const DateText = styled.div`
  font-size: 14px;
`
const WeekendText = styled.div`
  grid-column: 2;
  grid-row: 3;
  font-size: 16px;
`

const DigitalClock = () => {
  const [date, setDate] = useState(new Date());

  useEffect(() => {
    let interval = setInterval(() => {
      setDate(new Date());
    }, 1000)
    return () => {
      clearInterval(interval);
    }
  }, [])

  return (
    <Container>
      <TimeText>{dayjs(date).format('h : mm : ss')}</TimeText>
      <MeridiemText>{dayjs(date).format('A')}</MeridiemText>
      <DateText>{dayjs(date).format('YYYY년 M월 D일')}</DateText>
      <WeekendText>{dayjs(date).format('dddd')}</WeekendText>
    </Container>
  )
}
export default DigitalClock;